<template>
  <div>
    <div class="user-poster">
    </div>
    <div class="avatar-content">
        <img :src='user.avatar' alt="" v-if="user.avatar">
        <img src='../../static/logo-img.png' alt="" v-else>
      </div>
    <van-row class="user-links">
      {{user.name ? user.name+",":""}} 您好 ！
    </van-row>
    <van-cell-group>
      <van-cell icon="close" title="退出登陆" is-link @click="handleLogout"/>
    </van-cell-group>
  </div>
</template>

<script>
import { Row, Col, Icon, Cell, CellGroup, Dialog,Field } from "vant";
import { logout } from "../../utils/auth";

export default {
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Dialog.name]: Dialog,
    [Field.name]: Field
  },
  data() {
    return {
      user: {
        name: "",
        avatar: ""
      }
    };
  },
  created(){
    this.user.name=this.$store.state.user.name,
    this.user.avatar=this.$store.state.user.avatar
  },
  methods: {
    handleLogout() {
      Dialog.confirm({
        title: "退出登陆",
        message: "此操作将退出登陆，请确认"
      })
        .then(() => {
          logout();
          window.location = window.location.origin + "/manage/login?logout=true";
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>

